*,
*::before,
*::after {
	box-sizing: border-box;
	font-family: PTSans;
	font-style: normal;
	font-weight: 400;
}

.l-container {
  max-width: 1440px;
	padding: 0 70px;
  margin: 0 auto;
	@media screen and (max-width: 1090px) {
    padding: 0 40px;
  }
	@media screen and (max-width: 425px) {
    padding: 0 15px;
  }
}

.flex {
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
}

.--just-space {
	justify-content: space-between;
}

.--just-center {
	justify-content: center;
}

.--just-end {
	justify-content: flex-end;
}

.--align-str {
	align-items: stretch;
}

.--align-center {
	align-items: center;
}

.--align-end {
	align-items: flex-end;
}

.--dir-col {
	flex-direction: column;
}

html {
	font-size: 16px;
}

html,
body {
	min-height: 100%;
	position: relative;
}

body {
	font-size: 1rem;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
	list-style: none;
	padding: 0;

	font-family: PT Serif;
	font-style: normal;
	font-weight: normal;
}

a {
	color: #000;
	text-decoration: none;
	&:hover {
		color: #000;
	}
}